import React, { Component } from 'react'
import ReactDOM from 'react-dom'

// No RN but TouchableOpacity = Button ; AsyncStorage = LocalStorage
// NOTE: styles are local (below) + external @ web/css/styles.css
// NOTE: "index.web.js" not used for "entry flow" ; see inspiration = https://dev.to/kylessg/a-sensible-approach-to-cross-platform-development-with-react-and-react-native-57pk

// common or shared
import * as CONSTANTS from '../../common/constants.js'; // shared to both web + mobile
import * as SHARED from '../../common/shared.js'; // shared to both web + mobile
import * as WEB from '../../common/web.js'; // not shared - methods/functions for (web) component

// ...

// class (not funcational) component
class CustomCardContainer extends Component {
    // * Never call setState() inside constructor() ; use this.state instead
    // * The constructor is a method that’s automatically called during the creation of an object from a class. 
    // It can handle your initial setup stuff like defaulting some properties of the object, or sanity 
    // checking the arguments that were passed in. Simply put, the constructor aids in constructing things.
    // ref = https://alligator.io/react/constructors-with-react-components
    constructor(props) {
        console.log('@ constructor Custom Card')
        super(props);

        // OLD
        //SHARED.dataGet(); // 1st thing page load = get data

        // NEW
        WEB.dataGet(); // 1st thing page load = get data
        // og.note = not needed ? since there is no parent-child (component) communication
        //SHARED.appCheck('web')

        // ...

        // ref = https://reactjs.org/
        // With props, we have a one way data flow, but with state we can update private data from a component.
        // You can think of state as any data that should be saved and modified without necessarily being added 
        // to a database - for example, adding and removing items from a shopping cart before confirming a purchase.
        //state = { // fail
        this.state = {
            //active: false,
            isLoading: true, // spinner mech per https://appdividend.com/2018/04/02/react-loading-spinner-example/
            randomImage: '',
            message: '',
            imageLink: '',
            data: [],
          };

    }

    // NOT USE = SVG is auto format (default = no css needed)
    // note: if we use below = we have to define css + function = technical debt
    // ref = https://stackoverflow.com/questions/92720/jquery-javascript-to-replace-broken-images
    imgError(image) {
        console.log('img error called' +image)
        console.log('img error called' +image)
        //image.onError = '';
        image.style = 'maxWidth: 350, maxHeight: 350, width: 200, height: 200';
        image.src = 'https://storage.googleapis.com/gcs.hostbin.co/public/images/error_image.png';
        return true;
    }

    // ...

    // Only once componentDidMount has been called does React guarantee that componentWillUnmount will later be called for clean up.
    // ref = https://reactjs.org/blog/2018/03/27/update-on-async-rendering.html#adding-event-listeners-or-subscriptions
    
    // Constructor is the only place where you should assign this.state directly. In all other methods, you need to use this.setState() instead.
    // ref = https://reactjs.org/docs/react-component.html
    componentDidMount() {
        console.log('\n\n');
        // Call our API in componentDidMount lifecycle method.
        // **First:** constructor runs
        // **Second:** render runs
        // **Third:** componentDidMount runs
        console.log('@ constructor Custom Card - component Did Mount');        
        document.title = CONSTANTS.UI_TITLE

        //this.setState( { message: CONSTANTS.UI_WORKING })

        // this will update state variable (aka UI)
        // global variable - used in select areas in place of this.state
        
        // ref = https://stackoverflow.com/questions/31856712/update-component-state-from-outside-react-on-server-response
        SHARED.rnGlobal.cbmessage = (data) => {
            //SHARED.rnGlobal.callback = (data) => { // arbitrary name. It could be fooBar instead of callback
            this.setState({ message: data}); // `this` refers to our react component
        };

        // ref = https://stackoverflow.com/questions/31856712/update-component-state-from-outside-react-on-server-response
        SHARED.rnGlobal.cbimage = (data) => {
            //SHARED.rnGlobal.callback = (data) => { // arbitrary name. It could be fooBar instead of callback
            this.setState({ imageLink: data}); // `this` refers to our react component
        };       

        console.log('TEST imageLink = ' +this.state.imageLink)
        if (this.state.imageLink==='') {
            console.log('add msg here')
            this.setState({ message: CONSTANTS.UI_GETTING});
            setTimeout(function () { SHARED.uiClear() }, 150); 
        }

    }

    // ...

    // NOTE = By definition, state is not accessible from outside the component
    // means state will not return if we use external function file    
    // ref = https://stackoverflow.com/questions/29303456/reactjs-onclick-change-element


    // NOTE = if below format not used, function will auto load (not load on click)
    // onClick={() => { SHARED.appCheck('web') }}> 
    
    // <img src={this.state.imageLink} alt='' className='imgCenter' onError={(e)=>e.target.className('imgError') e.target.setAttribute('src','https://storage.googleapis.com/gcs.hostbin.co/public/images/error_image.png')} />
    // <img src={this.state.imageLink} alt='' className='imgCenter' onError={(e)=>this.imgError(this)} />

    render() {

    // og.note: not using CustomButton - since RN monorepo does not include Web as a platform (like iOS / Android)
    const { isLoaded, imageLink, message } = this.state // uses this.state
        
        //if(!this.state.imageLink) { 
        if(!this.state.imageLink) { 
            return (
                <div> 
                    <button 
                    className='buttonStyle textStyle'
                    onClick={() => { SHARED.appCheck('web') }}> 
                        <div className='textStyle'>{CONSTANTS.UI_BUTTON}</div>
                    </button>
                    <div className='stylesCenter'>{this.state.message}</div>
                </div>
                );
        } else { 
            return (
                <div>
                    <button 
                    className='buttonStyle textStyle'
                    onClick={() => { SHARED.appCheck('web') }}>
                        <div className='textStyle'>{CONSTANTS.UI_BUTTON}</div>
                    </button>
                    <div className='stylesCenter'>{this.state.message}</div>
                    {/* <div className='stylesCenter'>{this.state.imageLink}</div> */}
                    <img src={this.state.imageLink} alt='' className='imgCenter' onError={(e)=>e.target.setAttribute('src','https://storage.googleapis.com/gcs.hostbin.co/public/images/not_available.svg')} />

                    <div className='stylesCenter textFooter'><a href='https://contactstoolkit.com/legal/fair-use.html' target='_blank' rel='noopener noreferrer'>{CONSTANTS.UI_FAIR_USE}</a></div>

                </div>
            );
        } // end if

  }
}

// export class
export default CustomCardContainer