export const dataGet = () => {
//dataGet() { // use if all code stored in component
    console.log('\n');
    console.log('@ web.js - ' +CONSTANTS.LOG_DATA_GET); 
    //console.log(CONSTANTS.LOG_BUTTON_CLICKED); 
    const url = CONSTANTS.URL_DATA;

    // ref = https://reactjs.org/docs/faq-ajax.html
    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
           this.dataSave(result); // save json - original
           //console.log(result); // check data before saving ; note = each value has an number as key (red in console) 
        },
        // og.note: this is FETCH error (not random error)
        //          you will need to use img src onerror per https://stackoverflow.com/questions/34097560/react-js-replace-img-src-onerror
        // per docs: it's important to handle errors here instead of a catch() block so that we don't swallow exceptions from actual bugs in components.
        (error) => {
          console.log('fetch error');
        }
      )
    return
}

// ...

export const dataSave = (someData) => {
//dataSave(someData) { // use if all code stored in component
    console.log('\n');
    console.log('@ web.js - ' +CONSTANTS.LOG_DATA_SAVE); 
    //console.log('someData = ' +someData); // works - will show full json
    var now = Date.now(); // UTC includes time - number of milliseconds elapsed since January 1, 1970 
    var today = new Date(); // This provides output in UTC timezone
    console.log('now = ' +now);
    console.log('utc = ' +today);
    console.log('saving date + data');
    localStorage.setItem("o.old.now", now); // save data to localStorage
    localStorage.setItem("o.utc", today); // save data to localStorage
    
    // og.fixme: ensure random is random
    //someData = SHARED.iShuffle(someData); // shuffle data before save = can NOT do = will create circular loop

    localStorage.setItem("o.list", JSON.stringify(someData)); // stringify the entire array and store in localStorage
    //SHARED.globalObject.cbmessage(''); // works
    //SHARED.uiClear(); // clear div = UI feedback
}

// ...

export const clearLs = () => {
    console.log('\nclearing LS');
    localStorage.removeItem('o.list'); 
    localStorage.removeItem('o.old.now'); 
    localStorage.removeItem('o.utc'); 
}