export const iCheckFive = (aoldNow, aDevice) => {
    console.log('@ check five');
    // check old dates
    console.log('aoldNow = ' +aoldNow);
    console.log('aDevice = ' +aDevice);

    var now = Date.now(); // UTC includes time - number of milliseconds elapsed since January 1, 1970 
    var today = new Date(); // This provides output in UTC timezone
    console.log('now = ' +now);
    console.log('utc = ' +today);

    // check if 5 minutes old per https://stackoverflow.com/questions/45128519/javascript-check-if-date-object-is-at-least-5-minutes-old-nodejs
    var ONE_MIN = 1*60*1000; // 5 for 5 Minutes 
    var FIVE_MIN = 1*60*1000; // 5 for 5 Minutes 

    // aoldNow
    var oldDate = new Date(+aoldNow); // the + is needed for date conversion
    //var oldDate = new Date(+oldNow); // the + is needed for date conversion
    //console.log('old ' +oldDate);
    //console.log('N minutes ' +FIVE_MIN);
    //var timeMath = date - oldDate; // FAIL = wrong date format 
    var timeMath = now - oldDate; // same date formats

    if (timeMath > FIVE_MIN) {
        console.log('og.log > YES - more than N mins');
        console.log('time math ' +timeMath);
        console.log('time math ' +timeMath/ONE_MIN);
        //return DATA_YES // because of RN + shared code, cant return
        MOBILE.jsGlobal.dataStatus = CONSTANTS.DATA_YES
        if(MOBILE.jsGlobal.device === 'mobile') {
            console.log('continue mobile 1 \n')
            uiMessage(CONSTANTS.UI_FRESH_DATA)
            MOBILE.dataGet(); // get new data
            console.log('TEST TEST TEST');                
            //uiClear(); // FAIL = because flow is async ; will start but not finish
            iGetRandom()
            uiClear(); 
        } else if(MOBILE.jsGlobal.device === 'web') {
            console.log('continue web 1 \n')
            uiMessage(CONSTANTS.UI_FRESH_DATA)
            WEB.dataGet(); // get new data
            console.log('TEST TEST TEST');                
            iGetRandom()
            uiClear(); 
       }
    } else {
        console.log('og.log > NO = not old');
        console.log('time math ' +timeMath);
        console.log('time math ' +timeMath/ONE_MIN);
        iDataCache(); // use cache aka local storage
    }

    // og.note: should be removed or note remain
    // note: any code here will be async = do not use
    console.log('@ iCheckFive reminder = above executed via callback')
    //console.log(MOBILE.jsGlobal.dataStatus) // will be undefined because code is async
    return
}

