// og.note: this is sufficiently random aka pseudo-random
// ref = https://stackoverflow.com/a/2450976/1293256
// ref = https://gomakethings.com/how-to-shuffle-an-array-with-vanilla-js/
export const iShuffle = (array) => {
//export const iShuffle = (array, callback) => {
    console.log('@ shuffle');
    var currentIndex = array.length;
    console.log('@ shuffle - current index = ' +currentIndex)
    var temporaryValue, randomIndex;
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      //console.log('random index = ' +randomIndex)
      currentIndex -= 1;
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
}

